.scroll-to-top {
  position: fixed;
  right: 1rem;
  bottom: 1rem;
  color: white;
  background-color: rgb(var(--primary-color));
  z-index: 11;
  padding: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  transition: background-color 0.5s ease;
  box-shadow: 0px 0px 2px 4px rgb(var(--primary-color), 0.4);
}
.scroll-to-top:hover {
  background-color: rgb(var(--primary-color), 0.9);
  cursor: pointer;
}
